import { useEffect } from 'react';
import { Navigate, useParams } from 'react-router-dom';

import { versions } from './versions/';

const PreviewPage = () => {
  const { version, ...rest } = useParams();
  const validVersion = version && versions[version] ? version : undefined;

  useEffect(() => {
    document.title = `City Online Game Preview ${validVersion}`;
  });

  const Version = validVersion ? versions[validVersion] : undefined;

  return (
    <>
      {Version ? (
        <Version {...rest} />
      ) : (
        <Navigate to="/preview/game" replace={true} />
        // <div className="options-container">
        //   <OptionsBox title="City Online Game Preview">
        //     <ul>
        //       {Object.keys(versions).map((v) => (
        //         <li key={v}>
        //           <Link to={`/preview/${v}`} replace={false}>
        //             {v}
        //           </Link>
        //         </li>
        //       ))}
        //     </ul>
        //   </OptionsBox>
        // </div>
      )}
    </>
  );
};

export default PreviewPage;
