/**
 * Original solution by Jonathan Morales Vlez:
 * https://stackoverflow.com/a/61266099/192331
 */
import { useEffect, useRef } from 'react';
import styles from './style.module.css';

type Props = {
  className?: string;
  /* globals ScrollIntoViewOptions:readonly */
  behavior?: ScrollIntoViewOptions['behavior'];
};

const AlwaysScrollToBottom = ({ className, behavior }: Props) => {
  const elementRef = useRef<HTMLDivElement | null>(null);
  useEffect(() => {
    setTimeout(
      () =>
        elementRef?.current?.scrollIntoView({
          behavior,
        }),
      100,
    );
  }, [elementRef, behavior]);

  return <div ref={elementRef} className={`${styles['always-scroll-to-bottom']} ${className}`} />;
};

export default AlwaysScrollToBottom;
