// Update/bump up every time State structure is changed in order to
// invalidate any existing persisted states on the next (re)load
// This should be the only place where the version is explicitely
// stated, everything else should use the `stateVersion` const or

import { log } from 'utils';
import { ChatMessages, Post, Profile, Response } from './';

// `typeof stateVersion` as reference to improve maintenance.
export const STATE_VERSION = '202303141128';

export interface State {
  version: typeof STATE_VERSION;
  hash: string;
  step: number;
  responses: {
    [id: string]: Response;
  };
  validSteps: boolean[];
  profile: Profile;
  post: Post;
  messages: ChatMessages[];
  friend1: string;
  friend2: string;
  variables: {
    lostFriends: number;
    [key: string]: string | number | boolean;
  };
}

const validState = {
  version: STATE_VERSION,
  hash: '',
  step: 0,
  responses: {},
  validSteps: [],
  profile: {
    name: '',
    friends: 0,
    followers: 0,
    loaded: false,
  },
  post: {
    id: '',
    img: { src: '' },
    likes: 0,
    shares: 0,
    views: 0,
  },
  messages: [],
  friend1: '',
  friend2: '',
  variables: {
    lostFriends: 0,
  },
} as State as { [key: string]: any };

export const isState = (state: unknown): state is State => {
  log({
    stateType: typeof state,
    stateNull: state === null,
    stateLength: Object.keys(state || {}).length,
    validLength: Object.keys(validState).length,
    keyTypesValid: Object.entries(state || {}).every(
      ([key, value]) => typeof validState[key] === typeof value,
    ),
    stateTypes: Object.entries(state || {}).map(([k, v]) => ({ [k]: typeof v })),
    validTypes: Object.entries(validState || {}).map(([k, v]) => ({ [k]: typeof v })),
  });
  return (
    typeof state === 'object' &&
    state !== null &&
    Object.keys(state).length === Object.keys(validState).length &&
    Object.entries(state).every(([key, value]) => typeof validState[key] === typeof value)
  );
};

export const isCurrentState = (state: unknown): state is State => {
  return isState(state) && state.version === validState.version;
};

export interface Progress {
  current: number;
  total: number;
}
