import { SurveyStepConfigOptions } from 'pages/PreviewPage/versions/Version4/types';
import { ChangeEvent } from 'react';

interface Props {
  config: SurveyStepConfigOptions;
  selected?: string;
  setResponse: (response: string, invalid?: boolean) => void;
  next: () => void;
  moduleClasses: (classes: string) => string;
}

const SurveyOptions = ({ config, selected, moduleClasses, setResponse, next }: Props) => {
  const { options } = config;
  const handleOnChange = (e: ChangeEvent<HTMLInputElement>) => {
    setResponse(e.target.value);
    next();
  };

  return (
    <div className={moduleClasses('survey-options')}>
      {options.map(({ id, label }) => (
        <div key={id} className={moduleClasses(`survey-response ${selected === id && 'selected'}`)}>
          <label>
            <input
              type="radio"
              value={id}
              id={id}
              checked={selected === id}
              onChange={handleOnChange}
              onClick={() => selected === id && next()}
            />
            {label}
          </label>
        </div>
      ))}
    </div>
  );
};

export default SurveyOptions;
