// See: https://stackoverflow.com/a/46545530/192331
export const shuffle = <T>(unshuffled: T[]): T[] =>
  unshuffled
    .map((value) => ({ value, sort: Math.random() }))
    .sort((a, b) => a.sort - b.sort)
    .map(({ value }) => value);

export const log = (data: any) => {
  if (typeof data === 'object') {
    console.log({ __trace: new Error().stack?.split('\n')[2].match(/at ([^ ]+) /)?.[1], ...data });
  } else {
    console.log({ __trace: new Error().stack?.split('\n')[2].match(/at ([^ ]+) /)?.[1], data });
  }
};
