import { ReactNode } from 'react';

interface Props {
  from: string;
  message: ReactNode;
  moduleClasses: (classes: string) => string;
}

const Response = ({ from, message, moduleClasses }: Props) => {
  return (
    <div className={moduleClasses('content-item responses')}>
      <div className={moduleClasses('res-container')}>
        <div className={moduleClasses('msg-item res-name')}>{from}</div>
        <div className={moduleClasses('res-item res-photo')}></div>
        <div className={moduleClasses('res-item res-msg')}>{message}</div>
      </div>
    </div>
  );
};

export default Response;
