const Version4 = () => {
  const [state, dispatch] = usePersistedReducer(
    'surveyState',
    reducerWithConfig(config),
    initialState,
    initWithConfig(config),
    isCurrentState,
  );

  // useEffect(() => {
  //   const hh = async () => {
  //     const h = await hash(JSON.stringify(config));
  //     console.log(h);
  //   };
  //   hh();
  // });

  // const [{ step: stepState, responses }, setState] = useState(initialState);

  // const { step: stepParam } = useParams();

  // const maxStep = Object.keys(responses).length;
  // const urlStep = parseInt(stepParam || '-1', 10);
  // const step = !isNaN(urlStep) && urlStep >= 0 && urlStep < maxStep ? urlStep : maxStep;

  // console.log('state', state);
  const stepConfig = config[state.step];

  const progress: Progress = {
    current: 0,
    total: 0,
  };
  for (let i = 0; i < config.length; i++) {
    const { restartProgress, skipProgress } = config[i];
    const { current, total } = progress;

    if (i > state.step && restartProgress) break;

    progress.current =
      i > state.step ? current : restartProgress ? 1 : skipProgress ? current : current + 1;
    progress.total = restartProgress ? 1 : skipProgress ? total : total + 1;
  }

  // const navigate = useNavigate();
  // console.log(state, state.step);

  if (stepConfig && stepConfig.type === 'game') {
    return <Game config={stepConfig} state={state} dispatch={dispatch} />;
  } else {
    return <Survey config={stepConfig} state={state} progress={progress} dispatch={dispatch} />;
  }
};

export default Version4;
