/* eslint-disable no-unused-vars */
import { Response, SurveyStepConfigText } from 'pages/PreviewPage/versions/Version4/types';
import { ChangeEvent, useEffect, useRef } from 'react';
import styles from './style.module.css';

interface Props {
  config: SurveyStepConfigText;
  response?: Response;
  setResponse: (response: string, invalid?: boolean) => void;
  next: () => void;
  moduleClasses: (classes: string) => string;
}

const SurveyText = ({ response, config, moduleClasses, setResponse, next }: Props) => {
  const {
    inputLabel,
    inputMode,
    enterKeyHint,
    validator,
    partialValidator,
    transformer,
    formatter,
  } = config;
  const value = response?.response || '';
  const invalid = response?.invalid;

  // console.log({ response, value, invalid });

  const elementRef = useRef<HTMLInputElement | null>(null);
  useEffect(() => {
    elementRef.current?.focus();
  });

  const handleOnChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = transformer ? transformer(e.target.value) : e.target.value;
    if (value !== '' && partialValidator !== undefined && !partialValidator(value)) return;

    const valid = value === '' || validator === undefined || validator(value);

    setResponse(value, !valid);
  };

  // console.log(value ? (formatter ? formatter(value) : value) : '');
  return (
    <div className={moduleClasses('survey-text')}>
      <label className={`${styles.inp} ${invalid && styles.error}`}>
        <div className={styles.container}>
          <input
            type="text"
            inputMode={inputMode}
            enterKeyHint={enterKeyHint || 'next'}
            placeholder="&nbsp;"
            value={value ? (formatter ? formatter(value) : value) : ''}
            ref={elementRef}
            onChange={handleOnChange}
            onKeyDown={(e) => e.key === 'Enter' && value && !invalid && next()}
            className={invalid ? moduleClasses('invalid') : ''}
          />
          <span className={styles.label}>{inputLabel}</span>
          <span className={styles['focus-bg']}></span>
        </div>
      </label>
      {/* <div className={`${styles['error-msg']} ${!invalid && styles.hide}`}>
        {invalid && (errorMessage || 'Please enter valid value.')}
      </div> */}

      {/* <input
        type='text'
        value={value || ''}
        ref={elementRef}
        inputLabel={inputLabel}
        onChange={handleOnChange}
        onKeyDown={(e) => e.key === 'Enter' && value && !invalid && next()}
        className={invalid ? moduleClasses('invalid') : ''}
      />
      <div className={moduleClasses('error')}>
        {invalid && (errorMessage || 'Please enter valid value.')}
      </div> */}
    </div>
  );
};

export default SurveyText;
