import AlwaysScrollToBottom from 'components/AlwaysScrollToBottom';
import Messages from 'components/Messages';
import Post from 'components/Post';
import Profile from 'components/Profile';
import Response from 'components/Response';
import ResponsePrompt from 'components/ResponsePrompt';
import { useModuleBodyStylesEffect, useModuleStyles } from 'hooks/module-styles';
import {
  Action,
  ChatMessages,
  ChatProfile,
  GameResponseOption,
  GameStepConfig,
  Persona,
  State,
} from 'pages/PreviewPage/versions/Version4/types';
import { Dispatch, useEffect } from 'react';
import styles from './style.module.css';

const personas: Record<Persona, ChatProfile> = {
  expert: {
    id: 'expert',
    name: 'Robin (Cyber Security Expert)',
    avatar: 'expert.png',
  },
  sightshare: {
    id: 'sightshare',
    name: 'SightShare',
    avatar: 'expert.png',
  },
  friend1: {
    id: 'friend1',
    name: '',
    avatar: 'expert.png',
  },
  friend2: {
    id: 'friend2',
    name: '',
    avatar: 'expert.png',
  },
};

const mergeMessages = (
  stateMessages: ChatMessages[],
  stepMessages: ChatMessages[],
): ChatMessages[] => {
  if (stateMessages.length > 0 && stepMessages.length > 0) {
    const lastStateMsg = stateMessages.slice(-1)[0];
    const firstStepMsg = stepMessages.slice(0, 1)[0];

    if (
      lastStateMsg.type === 'message' &&
      firstStepMsg.type === 'message' &&
      lastStateMsg.from === firstStepMsg.from
    ) {
      return [
        ...stateMessages.slice(0, -1),
        {
          type: lastStateMsg.type,
          from: lastStateMsg.from,
          messages: [...lastStateMsg.messages, ...firstStepMsg.messages],
        },
        ...stepMessages.slice(1),
      ];
    }
  }

  return [...stateMessages, ...stepMessages];
};

interface Props {
  config: GameStepConfig;
  state: State;
  dispatch: Dispatch<Action>;
}

const Game = ({ config, state, dispatch }: Props) => {
  const moduleClasses = useModuleStyles(styles);
  useModuleBodyStylesEffect(styles);

  const { messages: stepMessages, response, id, loadHandler, shouldLoad } = config;
  const { step, profile, post, messages: stateMessages } = state;

  const shouldHandleLoad = loadHandler && shouldLoad && shouldLoad(state);

  useEffect(() => {
    if (shouldHandleLoad) {
      dispatch({ type: 'CUSTOM_HANDLER', handler: loadHandler });
    }
  }, [shouldHandleLoad, dispatch, loadHandler]);

  const messages = mergeMessages(stateMessages, stepMessages);

  const handleResponse = (responseOption: GameResponseOption) => {
    console.log({
      trace: new Error().stack?.split('\n')[1].match(/at ([^ ]+) /)?.[1],
      responseOption,
      response,
    });

    dispatch({
      type: 'RESPONSE',
      response: {
        id,
        invalid: false,
        response: responseOption.id,
      },
    });

    if (config.responseHandler) {
      dispatch({ type: 'CUSTOM_HANDLER', handler: config.responseHandler(responseOption) });
    }

    dispatch({
      type: 'SET_MESSAGES',
      messages: [
        ...messages,
        {
          type: 'response',
          message: responseOption.label,
        },
        ...mergeMessages(
          responseOption.answer ? [responseOption.answer] : [],
          response?.followUp ? [response?.followUp] : [],
        ),
      ],
    });

    dispatch({ type: 'STEP_SET_VALID', step, valid: true });

    if (responseOption.nextStepId) {
      dispatch({ type: 'GOTO_STEP_ID', id: responseOption.nextStepId });
    } else {
      dispatch({ type: 'NEXT' });
    }
  };

  const getChatProfile =
    (state: State) =>
    (persona: Persona): ChatProfile => {
      return {
        ...personas[persona],
        name:
          persona === 'friend1' || persona === 'friend2' ? state[persona] : personas[persona].name,
      };
    };

  const enrichMessage =
    (state: State) =>
    (message: string): string =>
      message.replace(/\$\{([^}]+)\}/g, (match, key = '') => {
        return state.variables[key] ? state.variables[key].toString() : '';
      });

  return (
    <div className={moduleClasses('container')}>
      <Profile {...profile} moduleClasses={moduleClasses} />
      <Post {...post} moduleClasses={moduleClasses} />
      <div className={moduleClasses('item content')}>
        <div className={moduleClasses('content-container')}>
          <>
            {messages.map((message, i) =>
              message.type === 'message' ? (
                <Messages
                  {...message}
                  key={i}
                  enrichMessage={enrichMessage(state)}
                  getChatProfile={getChatProfile(state)}
                  moduleClasses={moduleClasses}
                />
              ) : (
                <Response
                  key={i}
                  from={profile.name}
                  message={message.message}
                  moduleClasses={moduleClasses}
                />
              ),
            )}
          </>
          <ResponsePrompt
            response={response || { options: [{ id: 'ok', label: 'OK' }] }}
            handleResponse={handleResponse}
            moduleClasses={moduleClasses}
          />
          <AlwaysScrollToBottom behavior="smooth" />
        </div>
      </div>
    </div>
  );
};

export default Game;
