import Img from 'components/Img';
import { SurveyStepConfigImageOptions } from 'pages/PreviewPage/versions/Version4/types';

interface Props {
  config: SurveyStepConfigImageOptions;
  selected?: string;
  setResponse: (response: string, invalid?: boolean) => void;
  next?: () => void;
  moduleClasses: (classes: string) => string;
}

const SurveyImageOptions = ({ config, selected, setResponse, next, moduleClasses }: Props) => {
  const { options } = config;

  return (
    <div className={moduleClasses('survey-image-options')}>
      {options.map(({ id, image }) => (
        <button
          key={id}
          className={moduleClasses(`survey-image-option ${selected === id && 'selected'}`)}
          onClick={() => {
            setResponse(id);
            next?.();
          }}
        >
          <Img src={image} />
        </button>
      ))}
    </div>
  );
};

export default SurveyImageOptions;
