import { SurveyConfig } from '../types';

export const gamePost1Config: SurveyConfig = [
  {
    id: 'post-1-1',
    type: 'game',
    restartProgress: true,
    messages: [
      {
        type: 'message',
        from: 'friend1',
        messages: ['Love it!'],
      },
      {
        type: 'message',
        from: 'sightshare',
        messages: ['Oh, you received your first likes and gained new followers. Keep going .'],
      },
    ],
    response: {
      // prompt: 'Ready for the next post?',
      randomise: false,
      options: [
        {
          id: 'response-1',
          label: 'Lets go!',
        },
      ],
    },
    shouldLoad: (state) => !state.profile.loaded,
    loadHandler: (state) => {
      const friends = Math.max(parseInt(state.responses['friends']?.response || '100', 10), 10);
      return {
        ...state,
        profile: {
          name: state.responses['name']?.response || 'Jane Doe',
          avatar: state.responses['avatar']?.response || '',
          friends,
          followers: Math.round(friends * 0.1),
          loaded: true,
        },
        post: {
          id: 'post-1',
          img: {
            src: state.responses['first-post']?.response,
          },
          likes: Math.round(friends * 0.4),
          shares: Math.max(Math.round(friends * 0.08), 1),
          views: Math.round(friends * 1.2),
        },
        friend1: state.responses.friend1.response,
        friend2: state.responses.friend2.response,
      };
    },
  },
];
