import { useEffect } from 'react';

export interface ModuleStyles {
  readonly [key: string]: string;
}

// s/class="([^"]+)"/className={moduleClasses("$1")}/
export const useModuleStyles =
  (styles: ModuleStyles) =>
  (classesStr: string): string =>
    classesStr
      .split(/ +/)
      .map((c) => styles[c])
      .join(' ');

export const useModuleBodyStylesEffect = ({ body }: ModuleStyles) =>
  useEffect(() => {
    if (!body) return;

    document.body.classList.add(body);
    return () => {
      document.body.classList.remove(body);
    };
  });
