import { createBrowserRouter, Navigate, RouterProvider } from 'react-router-dom';

import ErrorPage from 'pages/ErrorPage';
import MainPage from 'pages/MainPage';
import PreviewPage from 'pages/PreviewPage';
import './App.css';

const App = () => {
  const router = createBrowserRouter([
    {
      path: '/',
      element: <MainPage />,
      errorElement: <ErrorPage />,
    },
    {
      path: '/preview',
      element: <PreviewPage />,
    },
    {
      // Redirect for legacy URLs
      // TODO: remove when cleaning up after couple of days/weeks
      path: '/preview/Version3',
      element: <Navigate to="/preview/OnlineGame" replace={true} />,
    },
    {
      path: '/preview/:version',
      element: <PreviewPage />,
    },
    {
      path: '/preview/:version/:step',
      element: <PreviewPage />,
    },
  ]);

  return <RouterProvider router={router} />;
};

export default App;
