import { Dispatch, useEffect, useReducer } from 'react';

export const getIsStateFnFor =
  <S>(validState: S) =>
  (state: unknown): state is S => {
    return (
      typeof state === 'object' &&
      state !== null &&
      Object.keys(state).length === Object.keys(validState as { [key: string]: any }).length &&
      Object.entries(state).every(
        ([key, value]) => typeof (validState as { [key: string]: any })[key] === typeof value,
      )
    );
  };

export const usePersistedReducer = <State, Action>(
  stateKey: string,
  reducer: (state: State, action: Action) => State,
  initialState: State,
  initFn?: (initialState: State) => State,
  isStateFn?: (state: unknown) => state is State,
): [State, Dispatch<Action>] => {
  const init = (): State => {
    const initState = typeof initFn !== 'undefined' ? initFn(initialState) : initialState;
    const isState = typeof isStateFn !== 'undefined' ? isStateFn : getIsStateFnFor(initialState);

    try {
      const persistedStateStr = window.localStorage.getItem(stateKey);
      const persistedState = persistedStateStr ? JSON.parse(persistedStateStr) : undefined;
      return isState(persistedState) ? persistedState : initState;
    } catch (e) {
      return initState;
    }
  };

  const [state, dispatch] = useReducer(reducer, undefined, init);
  const stateStr = JSON.stringify(state);

  useEffect(() => {
    window.localStorage.setItem(stateKey, stateStr);
  }, [stateKey, stateStr]);

  return [state, dispatch];
};
