import SurveyRange from 'components/SurveyRange';
import {
  Action,
  State,
  SurveyStepConfigRangeGroup,
} from 'pages/PreviewPage/versions/Version4/types';
import { Dispatch } from 'react';

interface Props {
  state: State;
  dispatch: Dispatch<Action>;

  config: SurveyStepConfigRangeGroup;
  moduleClasses: (classes: string) => string;
}

const SurveyRangeGroup = ({ config, state, dispatch, moduleClasses }: Props) => {
  const { ranges } = config;
  const { step, responses } = state;

  const invalidRanges = ranges
    .map((range) => range.id)
    .filter((id) => typeof responses[id] === 'undefined');

  const handleOnChange = (id: string) => (response: string) => {
    dispatch({ type: 'RESPONSE', response: { id, response, invalid: false } });

    // Only auto proceed if we select last range, but not when we go back and change any range:
    if (invalidRanges.length === 1 && invalidRanges.includes(id)) {
      dispatch({ type: 'STEP_SET_VALID', step, valid: true });
      dispatch({ type: 'NEXT' });
    }
  };

  return (
    <div className={moduleClasses('survey-options')}>
      {ranges.map((rangeConfig) => (
        <SurveyRange
          key={rangeConfig.id}
          config={{ ...rangeConfig, type: 'range', rangeLabel: rangeConfig.label }}
          selected={responses[rangeConfig.id]?.response}
          setResponse={handleOnChange(rangeConfig.id)}
          moduleClasses={moduleClasses}
        />
      ))}

      {/* <button
        onClick={() => {
          ranges.forEach(({ id }) => {
            dispatch({ type: 'RESET_RESPONSE', id });
          });
          dispatch({ type: 'STEP_SET_VALID', step, valid: false });
        }}
      >
        Reset
      </button> */}
    </div>
  );
};

export default SurveyRangeGroup;
