import ResponseButton from 'components/ResponseButton';
import { GameResponse, GameResponseOption } from 'pages/PreviewPage/versions/Version4/types';
import { shuffle } from 'utils';

interface Props {
  response: GameResponse;
  handleResponse: (response: GameResponseOption) => void;
  moduleClasses: (classes: string) => string;
}

const ResponsePrompt = ({
  response: { prompt, randomise, options },
  handleResponse,
  moduleClasses,
}: Props) => {
  // By default, randomise options:
  const responses = randomise === undefined || randomise ? shuffle(options) : options;

  return (
    <div className={moduleClasses('content-item responses')}>
      <div className={moduleClasses('res-container')}>
        <div className={moduleClasses('res-item res-prompt')}>{prompt}</div>
        <div className={moduleClasses('res-item res-photo')}></div>
        {responses.map((response) => (
          <div key={response.id} className={moduleClasses('res-item response')}>
            <ResponseButton id={response.id} onClick={() => handleResponse(response)}>
              {response.label}
            </ResponseButton>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ResponsePrompt;
